.contact {
  position: absolute;
  width: 100vw;
  height: 100vh;

  .skills{
    width: 50vw;

  }
  .skills-list{
    display: flex;
    justify-content: space-between;
    color: white;
    font-size: 1.5rem;

    
  }

  h4 {
    color: red;
    font-size: 1.3rem;
    font-weight: bold;
    margin-top: 50px;
    margin-bottom: 10px;

    @media screen and (min-width: 1200px){
      font-size: 1.8rem;
    }
  }
  p {
    @media screen and (min-width: 1200px){
      color: white;
      font-size: 1.5rem;
    }
  }
}
  .contact-infos{
    color: white;
    margin-top: 5%;
    margin-left: 2%;

    display: flex;
    flex-direction: column;
    height: 200px;
    justify-content: space-between;

}
